export default function Header(props) {
  const st = props && !!props.solid;
  return <Navbar style={st ? wrapperSolid : wrapper } expand="lg">
  <Navbar.Brand href="/">
      <img src="/new_logo1.png"
       width="50"
        height="50"
        className="d-inline-block align-top"
       alt="Hey, Lion!"></img>
  </Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="mr-auto">
        <Nav.Link href="/whoami">Обо мне</Nav.Link>
        <Nav.Link href="/study">Обучение online</Nav.Link>
        <Nav.Link href="/gallery">Галерея работ</Nav.Link>
    </Nav>
  </Navbar.Collapse>
</Navbar>
}
